<template>
  <div class="row">
    <div class="col-12">
      <card :title="table1.title" :subTitle="table1.subTitle">
        <div slot="raw-content" class="table-responsive">
          <table>
            <thead>
              <tr>
                <th>Username</th>
                <th>Nome</th>
                <th>Email</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in listaUtilizadores" :key="item.id">
                <td>{{ item.username }}</td>
                <td>{{ item.nome }}</td>
                <td>{{ item.email }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      table1: {
        title: "Lista dos Utilizadores",
        subTitle: "Lista de Utilizadores logados",
      },
      listaUtilizadores: [], // Inicializa como uma lista vazia
    };
  },
  mounted() {
    this.carregarDadosLocalStorage();
  },
  methods: {
    carregarDadosLocalStorage() {
      const dados = JSON.parse(localStorage.getItem("utilizadores"));
      if (dados) {
        this.listaUtilizadores = dados;
      }
    },
  },
};
</script>

