<template>
  <div>
    <ul>
      <li v-for="reserva in reservas" :key="reserva.id">
        <div>{{ reserva.nome }}</div>
        <div>{{ reserva.data }}</div>
        <div>{{ reserva.hora }}</div>
        <div>{{ reserva.aceite ? 'Aceite' : 'Pendente' }}</div>
        <div>
          <button v-if="!reserva.aceite" @click="aceitarReserva(reserva)">Aceitar</button>
          <button v-if="!reserva.aceite" @click="rejeitarReserva(reserva)">Rejeitar</button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      reservas: [
        { id: 1, nome: 'Campo A', data: '2023-05-31', hora: '14:00', aceite: false },
        { id: 2, nome: 'Campo B', data: '2023-06-01', hora: '15:30', aceite: true },
        { id: 3, nome: 'Campo C', data: '2023-06-02', hora: '17:00', aceite: false }
      ]
    };
  },
  methods: {
    aceitarReserva(reserva) {
      reserva.aceite = true;
      // Lgica adicional para atualizar o estado da reserva no backend ou notificar o cliente
    },
    rejeitarReserva(reserva) {
      reserva.aceite = false;
      // Lgica adicional para atualizar o estado da reserva no backend ou notificar o cliente
    }
  }
};
</script>
