<template>
  <div class="lesson-schedule">
    <h2>Horrio de Aulas Marcadas</h2>
    <table>
      <thead>
        <tr>
          <th>Hora</th>
          <th>Segunda-feira</th>
          <th>Tera-feira</th>
          <th>Quarta-feira</th>
          <th>Quinta-feira</th>
          <th>Sexta-feira</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="time in times" :key="time">
          <td>{{ time }}</td>
          <td v-for="day in days" :key="day">
            <div v-if="hasLesson(day, time)">
              <div v-for="lesson in getLessons(day, time)" :key="lesson.id">
                <p><strong>Professor:</strong> {{ lesson.professor }}</p>
                <p><strong>Aluno:</strong> {{ lesson.student }}</p>
                <p><strong>Hora:</strong> {{ lesson.time }}</p>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      days: ['Segunda-feira', 'Tera-feira', 'Quarta-feira', 'Quinta-feira', 'Sexta-feira'],
      times: ['09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00'],
      lessons: [
        { id: 1, professor: 'Joo Silva', day: 'Segunda-feira', time: '09:00', student: 'Maria Santos' },
        { id: 2, professor: 'Pedro Oliveira', day: 'Tera-feira', time: '11:00', student: 'Carlos Mendes' },
        { id: 3, professor: 'Ana Rodrigues', day: 'Quarta-feira', time: '14:00', student: 'Marta Almeida' },
        // ... outras aulas marcadas de padel
      ]
    };
  },
  methods: {
    hasLesson(day, time) {
      return this.lessons.some(lesson => lesson.day === day && lesson.time === time);
    },
    getLessons(day, time) {
      return this.lessons.filter(lesson => lesson.day === day && lesson.time === time);
    }
  }
};
</script>

